<template>
  <div>
    <div id="stars" v-show="!showMain"></div>
    <div id="blank" v-show="!showMain"></div>
    <div class="container" v-show="showMain">
      <div class="box-center">
        <div class="box-aside">
          <div class="aside box">
            <v-avatar
                :class="['pp', (loading ? 'animated-background': '')]"
                color="#eaeaea"
                size="150">
              <img
                  :src="profile.image"
                  :alt="profile.name"
                  v-show="!loading"
              >
            </v-avatar>
            <div :class="['name', (loading ? 'animated-background': '')]" v-html="profile.name"></div>
            <div :class="['emblem', (loading ? 'animated-background': '')]" v-html="profile.status"></div>
            <div :class="['top-menu', (loading ? 'animated-background': '')]">
              <v-icon color="#000000" class="menu top-icon" v-for="(menu, index) in profile.topMenu" v-bind:key="index" @click="(menu.link == '#copy'? copy(menu.title) : openNewTab(menu.link) )">{{ menu.icon }}</v-icon>
            </div>
            <div :class="['bottom-menu', (loading ? 'animated-background': '')]">
              <div class="bottom-sub-menu" v-for="(menu, index) in profile.bottomMenu" v-bind:key="index" @click="(menu.link == '#copy'? copy(menu.title) : openNewTab(menu.link) )">
                <v-icon color="#000000" class="menu top-icon bottom-icon">{{ menu.icon }}</v-icon>
                <span class="">{{ menu.title }}</span>
              </div>
            </div>
            <router-link to="/email-me">
              <button :class="['send-mail', (loading ? 'animated-background': '')]"><span v-show="!loading">Email Me</span></button>
            </router-link>
          </div>
        </div>
        <div class="box-main">
          <div class="main box">
            <div :class="['main-menu', { active: openMenu }]">
              <div :class="['button-menu', { close: openMenu }]" @click="mainMenu">
                <div class="line-menu-1"></div>
                <div class="line-menu-2"></div>
                <div class="line-menu-3"></div>
              </div>
              <nav :class="{ active: openMenuDelay }">
                <router-link to="/about-me" :class="['link-menu', ($route.name == 'home'? 'router-link-exact-active active':'')]">About Me</router-link>
                <router-link to="/skills" class="link-menu">Skills</router-link>
                <router-link to="/portfolio" class="link-menu">Portfolio</router-link>
              </nav>
            </div>
            <router-view></router-view>
          </div>
        </div>
      </div>
      <footer>&copy; {{new Date().getFullYear()}} Aditya Ayatusy Syifa.</footer>
    </div>
  </div>

</template>

<script>
import * as THREE from 'three';
import Swal from "sweetalert2";
import Encryption from "@/plugins/Encryption";
export default {
  name: 'App',

  components: {
  },
  beforeMount() {
    this.cekSession();
    this.getProfile();
    if(sessionStorage.getItem("porfolio-aditya") == "true"){
      this.showMain = true;
    }

    this.showStar();
  },
  data: () => ({
    profile: {},
    openMenu: true,
    openMenuDelay: true,
    openTimeout: null,
    loading: true,
    showMain: false
  }),
  methods:{
    mainMenu:function (){
      let self = this;
      if(this.openMenu){
        this.openMenu = false;
        this.openMenuDelay = this.openMenu;
      }else{
        this.openMenu = true;
        clearTimeout(this.openTimeout);
        this.openTimeout = setTimeout(() => {
          self.openMenuDelay = self.openMenu
        },300)
      }
    },
    getProfile:function (){
      this.axios.get("profile",{
          headers: { Authorization: `Bearer ${this.getToken()}` }
      }).then((response) => {
        this.loading = false;
        this.profile = response.data;
        setTimeout(() => {
          document.getElementById("blank").style.opacity = 1
          setTimeout(() =>{
            this.showMain = true
            //set session for remove data if close tab
            if (typeof(Storage) !== "undefined") {
              sessionStorage.setItem("porfolio-aditya", true);
            } else {
              console.log("Maaf, Browser kamu tidak support web storage");
            }
          },2000)
        },4000)
      }).catch((error) =>{
        setTimeout(() =>{
          this.getProfile();
        },5000)
        console.log(error);
      })
    },
    openNewTab:function (url){
      window.open(url, "_blank");
    },
    showStar:function (){
      var scene, camera, renderer;
      var isRunning = true;
      var speed = 0.02;
      var lenSpeed = speed + 0.001;

      let LINE_COUNT = 5000;
      let geom = new THREE.BufferGeometry();
      geom.setAttribute("position", new THREE.BufferAttribute(new Float32Array(6*LINE_COUNT), 3));
      geom.setAttribute("velocity", new THREE.BufferAttribute(new Float32Array(2*LINE_COUNT), 1));
      let pos = geom.getAttribute("position");
      let pa = pos.array;
      let vel = geom.getAttribute("velocity");
      let va = vel.array;

      function init() {
        scene = new THREE.Scene();
        camera = new THREE.PerspectiveCamera(60, window.innerWidth/window.innerHeight, 1, 500);
        camera.position.z = 100;

        renderer = new THREE.WebGLRenderer({antialias:true});
        renderer.setSize(window.innerWidth, window.innerHeight);
        setTimeout(()=>{
          document.getElementById("stars").appendChild(renderer.domElement);

          for (let line_index= 0; line_index < LINE_COUNT; line_index++) {
            var x = Math.random() * 400 - 200;
            var y = Math.random() * 200 - 100;
            var z = Math.random() * 500 - 100;
            var xx = x;
            var yy = y;
            var zz = z;
            //line start
            pa[6*line_index] = x;
            pa[6*line_index+1] = y;
            pa[6*line_index+2] = z;
            //line end
            pa[6*line_index+3] = xx;
            pa[6*line_index+4] = yy;
            pa[6*line_index+5] = zz;

            va[2*line_index] = va[2*line_index+1]= 0;
          }
          //debugger;
          let mat = new THREE.LineBasicMaterial({color: 0xffffff});
          let lines = new THREE.LineSegments(geom, mat);
          scene.add(lines);

          window.addEventListener("resize", onWindowResize, false);
          animate();
        },1000)
      }
      function onWindowResize() {
        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();
        renderer.setSize(window.innerWidth, window.innerHeight);
      }
      function animate() {
        for (let line_index= 0; line_index < LINE_COUNT; line_index++) {
          va[2*line_index] += speed; //bump up the velocity by the acceleration amount
          va[2*line_index+1] += lenSpeed;

          //pa[6*line_index]++;                       //x Start
          //pa[6*line_index+1]++;                     //y
          pa[6*line_index+2] += va[2*line_index];     //z
          //pa[6*line_index+3]++;                     //x End
          //pa[6*line_index+4]                        //y
          pa[6*line_index+5] += va[2*line_index+1];   //z

          if(pa[6*line_index+5] > 200) {
            var z= Math.random() * 200 - 100;
            pa[6*line_index+2] = z;
            pa[6*line_index+5] = z;
            va[2*line_index] = 0;
            va[2*line_index+1] = 0;
          }
        }
        pos.needsUpdate = true;
        renderer.render(scene,camera);
        if(!isRunning)
          return
        requestAnimationFrame(animate);

      }
      init();
      setTimeout(() => {
        isRunning = false
      },100)
      setTimeout(() => {
        isRunning = true
        animate();
      },1000)
    },
    //cek session for auto remove data if close tab / browser
    cekSession(){
      setTimeout(()=>{
        new BroadcastChannel('porfolio-cek').addEventListener('message', function(e){
          if(e.data == "porfolio"){
            new BroadcastChannel('porfolio-oke').postMessage('open');
          }
        });
      }, 1000);

      let session = sessionStorage.getItem("porfolio-aditya");

      if(session != "true"){
        try{
          new BroadcastChannel('porfolio-cek').postMessage('porfolio');
          new BroadcastChannel('porfolio-oke').addEventListener('message', function(e){
            if(e.data == "open"){
              sessionStorage.setItem("porfolio-aditya", true);
            }
          })
        }catch (e){
          console.log(e)
        }
      }
    },
    copy:function (text){
      try{
        navigator.clipboard.writeText(text.toLowerCase());
        const Toast = Swal.mixin({
          toast: true,
          position: 'top-end',
          showConfirmButton: false,
          timer: 3000,
          timerProgressBar: true
        })

        Toast.fire({
          icon: 'success',
          title: 'Success copy text '
        })
      }catch (e) {
        console.log(e)
      }
    },
    getToken:function (){
      let d = new Date(new Date().toLocaleString("en-US", {timeZone: "Asia/Jakarta"}));
      let time = d.getTime().toString();
      time = time.substring(0,time.length-3)
      let text = {
        'token':process.env.VUE_APP_TOKEN,
        'time': Number(time)
      };
      let encryption = new Encryption();
      const token = encryption.encrypt(JSON.stringify(text), process.env.VUE_APP_KEY);
      return token;
    }
  }
};

</script>

