<template>
  <div class="main-box">
    <div class="title">About Me</div>
    <div class="line-title"></div>
    <div :class="['desc', (loading ? 'animated-background': '')]" v-html="about.desc">
    </div>
    <button :class="['btn-resume', (loading ? 'animated-background': '')]" @click="$parent.openNewTab(about.resume)">
      <span v-show="!loading">My Resume</span>
    </button>
  </div>
</template>

<script>
export default {
  name: "About",
  beforeMount() {
    this.getAbout();
  },
  data: () => ({
      about: {
        desc:"<div style='height: 20px;width: 5%;float: left;background: white;'></div></div><div style='background: white;height: 10px; clear: both;'></div><div style='height: 20px'></div><div style='background: white;height: 10px'></div><div style='height: 20px;width: 20%;float: right;background: white;'></div>"
      },
      loading: true
  }),

  methods:{
    getAbout:function (){
      this.axios.get("about",{
        headers: { Authorization: `Bearer ${this.$parent.getToken()}` }
      }).then((response) => {
        this.loading = false;
        this.about = response.data;
      }).catch((error) =>{
        console.log(error);
      })
    }
  }
}
</script>

<style scoped>

  .desc{
    text-indent: 20px;
    margin-top: 20px;
    margin-bottom: 60px;
    margin-left: 30px;
    margin-right: 30px;
    min-height: 80px;
  }

  .btn-resume{
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translate(-50%, 0%);
    width: 400px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border-radius: 20px;
    background-color: var(--ad-blue);
    color: white;
    font-size: 12px;
    font-weight: bold;
  }


  @media screen and (max-width: 960px) {
    .line-title{
      width: 121px;
    }

    .btn-resume {
      width: 80%;
    }
  }


</style>