<template>
  <div class="main-box">
    <div class="title">Email Me</div>
    <div class="line-title"></div>
    <form :class="['desc', 'needs-validation', (validate? 'was-validated':'')]">
      <h5 class="mt-2 text-center mb-4">SEND US A NOTE</h5>
      <div class="row mb-3">
        <div class="col-6">
          <input type="text" class="form-control" id="name" placeholder="Name" v-model="name" required>
          <div class="invalid-feedback">
            Please provide a valid name.
          </div>
        </div>
        <div class="col-6">
          <input type="email" class="form-control" id="email" placeholder="Email" v-model="email" required>
          <div class="invalid-feedback">
            Please provide a valid email.
          </div>
        </div>
      </div>
      <div class="mb-3">
        <textarea class="form-control" id="text" rows="10" placeholder="Tell us more about you needs....." v-model="message" required></textarea>
        <div class="invalid-feedback">
          Please provide a valid message.
        </div>
      </div>
    </form>
    <button class="btn-send" :style="(disableBtn? 'background: var(--ad-dark-blue-grey)' : '')" @click="sendMessage" :disabled="disableBtn">Send Message</button>
  </div>
</template>

<script>
import Swal from "sweetalert2";

export default {
  name: "EmailMe",
  data: () => ({
    name: "",
    email:"",
    message:"",
    validate: false,
    disableBtn: false,
    toast: null
  }),
  methods:{
    sendMessage:function (){
      this.showLoadingData();
      this.disableBtn = true;
      this.validate = false;
      var forms = document.querySelector('.needs-validation')
      if(forms.checkValidity()){
        var data = {
          name: this.name,
          email: this.email,
          message: this.message
        }
        // eslint-disable-next-line no-unused-vars
        this.axios.post("contact", data ,{ headers: { "Content-Type": "application/json", Authorization: `Bearer ${this.$parent.getToken()}`} }).then((response) => {
          this.hideLoadingData();

          Swal.fire(
              'Success',
              'successfully sent',
              'success'
          )
          this.name = "";
          this.email = "";
          this.message = "";
          this.disableBtn = false;
        }).catch((error) =>{
          // this.showAlert(error)
          this.hideLoadingData();
          this.disableBtn = false;
          console.log(error);
        })
      }else{
        this.validate = true;
      }

    },
    showLoadingData(){
      this.toasts = Swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
      })

      this.toasts.fire({
        icon: 'success',
        title: "Loading....",
        didOpen: () => {
          Swal.showLoading()
        }
      })
    },
    hideLoadingData(){
      Swal.getTitle().textContent = "Success send mail"
      Swal.hideLoading()
      setTimeout(() => {
        this.toasts.close();
        this.toasts = null;
      }, 1000)
    }
  }
}
</script>

<style scoped>


.desc{
  text-indent: 20px;
  margin-top: 20px;
  margin-bottom: 60px;
  margin-left: 30px;
  margin-right: 30px;
}


.btn-send{
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translate(-50%, 0%);
  width: 400px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border-radius: 20px;
  background-color: var(--ad-blue);
  color: white;
  font-size: 12px;
  font-weight: bold;
}

textarea{
  resize: none;
  background-color: transparent;
}

textarea:focus{
  background-color: transparent;
}

@media screen and (max-width: 960px) {

  .line-title {
    width: 108px;
  }

  .btn-send {
    width: 80%;
  }

  .btn-send {
    position: relative;
  }

  .main-box{
    margin-bottom: 100px;
  }
}
</style>