<template>
  <div class="main-box">
    <div class="title">Portfolio</div>
    <div class="line-title"></div>
    <div class="list">
      <div class="row row-cols-3 m-auto desktop" >
        <div class="col text-center" v-show="loading">
          <div class="mini-box">
            <div class="image animated-background"></div>
          </div>
        </div>

        <div class="col text-center item-list" v-for="(item, index) in portfolio" v-bind:key="index">
          <div class="mini-box" @click="$parent.openNewTab(item.link)">
            <div class="image">
              <img :src="item.image" alt="php">
            </div>
          </div>
          <span class="text" v-html="item.title"></span>
        </div>

      </div>
      <div class="row row-cols-2 m-auto mobile" >
        <div class="col text-center" v-show="loading">
          <div class="mini-box">
            <div class="image animated-background"></div>
          </div>
        </div>

        <div class="col text-center item-list" v-for="(item, index) in portfolio" v-bind:key="index">
          <div class="mini-box" @click="$parent.openNewTab(item.link)">
            <div class="image">
              <img :src="item.image" alt="php">
            </div>
          </div>
          <span class="text" v-html="item.title"></span>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Portfolio",
  beforeMount() {
    this.getPortfolio();
  },
  data: () => ({
    portfolio: {},
    loading: true
  }),
  methods:{
    getPortfolio:function (){
      this.axios.get("portfolio",{
        headers: { Authorization: `Bearer ${this.$parent.getToken()}` }
      }).then((response) => {
        this.loading = false;
        this.portfolio = response.data.data;
        setTimeout(function (){
          let list = document.querySelectorAll(".desktop .item-list");
          list.forEach((v, k)=>{
            setTimeout(()=>{
              v.classList.add("show")
            }, k * 100);
          })

          let listMobile = document.querySelectorAll(".mobile .item-list");
          listMobile.forEach((v, k)=>{
            setTimeout(()=>{
              v.classList.add("show")
            }, k * 100);
          })

        }, 200);
      }).catch((error) =>{
        console.log(error);
      })
    }
  }
}
</script>

<style scoped>
  .mini-box{
    height: 140px;
    width: 100%;
    overflow: hidden;
    background-color: white;
    box-shadow: 0.1px 2px 8px rgba(0, 0, 0, 0.20);
    display: inline-block;
    border-radius: 10px;
    margin-top: 10px;
    position: relative;
    cursor: pointer;
  }

  .image{
    overflow: hidden;
    height: 140px;
  }

  .image img{
    width: 100%;
    height: 140px;
  }

  .text{
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .mobile{
    display: none;
  }

  @media screen and (max-width: 960px) {
    .line-title{
      width: 108px;
    }
    .mobile{
      display: flex;
    }
    .desktop{
      display: none;
    }
    /*.mini-box, .image{*/
    /*  height: 100px;*/
    /*}*/

    .text{
      font-size: 14px;
    }

    .list{
      overflow: scroll;
    }

  }
</style>