<template>
  <div class="main-box">
    <div class="title">Skills</div>
    <div class="line-title"></div>
    <div class="list">
      <div class="row row-cols-3 m-auto" >
        <div class="col" v-show="loading">
          <div class="mini-box">
            <div class="animated-background" style=" height: 100px;">
            </div>
            <div class="progress-box">
              <div class="progress-status">
                <div class="progress animated-background">
                  <div class="progress-bar"></div>
                </div>
              </div>
              <span class="load"></span>
            </div>
          </div>
        </div>
        <div class="col" v-show="loading">
          <div class="mini-box">
            <div class="animated-background" style=" height: 100px;">
            </div>
            <div class="progress-box">
              <div class="progress-status">
                <div class="progress animated-background">
                  <div class="progress-bar"></div>
                </div>
              </div>
              <span class="load"></span>
            </div>
          </div>
        </div>
        <div class="col" v-show="loading">
          <div class="mini-box">
            <div class="animated-background" style=" height: 100px;">
            </div>
            <div class="progress-box">
              <div class="progress-status">
                <div class="progress animated-background">
                  <div class="progress-bar"></div>
                </div>
              </div>
              <span class="load"></span>
            </div>
          </div>
        </div>

        <div class="col item-list" v-for="(skill, index) in skills" v-bind:key="index">
          <div class="mini-box" @click="openTab(skill.title)">
            <div class="image">
              <img :src="skill.image" :alt="skill.title">
            </div>
            <div class="progress-box">
              <div class="progress-status">
                <div class="progress">
                  <div class="progress-bar" :style="'width: '+skill.progress+'%;'" role="progressbar" :aria-valuenow="skill.progress" aria-valuemin="0" aria-valuemax="100"></div>
                </div>
              </div>{{ skill.progress }}%
            </div>
            <div class="desc">
              <div class="text" v-html="skill.title"></div>
            </div>
          </div>
        </div>

      </div>
    </div>

    <div class="list-mobile">
        <div class="col" v-show="loading">
          <div class="mini-box">
            <div class="animated-background" style=" height: 100px;">
            </div>
          </div>
        </div>

        <div class="col item-list" v-for="(skill, index) in skills" v-bind:key="index">
          <div class="mini-box" @click="openTab(skill.title)">
            <div class="text" v-html="skill.title"></div>
            <div class="progress-box">
              <div class="progress-status">
                <div class="progress">
                  <div class="progress-bar" :style="'width: '+skill.progress+'%;'" role="progressbar" :aria-valuenow="skill.progress" aria-valuemin="0" aria-valuemax="100"></div>
                </div>
              </div>
              {{ skill.progress }}%
            </div>

          </div>
        </div>

    </div>
  </div>
</template>

<script>
export default {
  name: "Skills",
  beforeMount() {
    this.getSkills();
  },
  data: () => ({
    skills: {},
    loading: true,
  }),
  methods:{
    openTab: function (keyword){
      window.open("https://www.google.com/search?q="+keyword, '_blank');
    },
    getSkills:function (){
      this.axios.get("skills",{
        headers: { Authorization: `Bearer ${this.$parent.getToken()}` }
      }).then((response) => {
        this.loading = false;
        this.skills = response.data.data;
        setTimeout(function (){
          let list = document.querySelectorAll(".list .item-list");
          list.forEach((v, k)=>{
            setTimeout(()=>{
              v.classList.add("show")
            }, k * 100);
          })

          let listMobile = document.querySelectorAll(".list-mobile .item-list");
          listMobile.forEach((v, k)=>{
            setTimeout(()=>{
              v.classList.add("show")
            }, k * 100);
          })

        }, 200);

      }).catch((error) =>{
        console.log(error);
      })
    }
  }
}
</script>

<style scoped>
    .mini-box{
      height: 140px;
      width: 100%;
      overflow: hidden;
      background-color: white;
      box-shadow: 0.1px 2px 8px rgba(0, 0, 0, 0.20);
      display: inline-block;
      border-radius: 10px;
      margin: 10px 0px;
      position: relative;
      cursor: pointer;
    }

    .image{
      overflow: hidden;
      height: 100px;
      background: var(--ad-dark-blue-grey);
    }

    .image img{
      width: 100%;
    }

    .progress-box{
        width: 100%;
        padding: 7px;
        font-size: 16px;
    }

    .progress{
      height: 10px;
      border-radius: 10px;
    }

    .progress-status{
      display: inline-block;
      width: 79%;
      margin-right: 5px;
    }

    .desc{
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba(0, 0, 0, 0.7);
      opacity: 0;
      text-align: center;
      line-height: 140px;
    }

    .text{
      color: white;
      font-weight: bold;
      font-size: 18px;
    }

    .mini-box:hover .desc{
      opacity: 1;
    }

    .list{
      overflow: auto;
    }

    .list-mobile{
      display: none;
      position: absolute;
      top: 60px;
      bottom: -20px;
      left: 0px;
      right: 0px;
      padding: 20px;
      background-color: var(--ad-blue-grey);
    }

    @media screen and (max-width: 1152px) {
      .progress-status {
        width: 70%;
      }
      .mini-box{
        height: 130px;
      }

      .image{
        height: 90px;
      }
    }

    @media screen and (max-width: 960px) {
      .line-title{
        width: 70px;
      }
      .list{
        display: none;
      }
      .list-mobile{
        display: block;
        overflow: scroll;
      }

      .list-mobile .mini-box{
        height: 80px;
        width: 100%;
        overflow: hidden;
        background-color: white;
        box-shadow: 0.1px 2px 8px rgba(0, 0, 0, 0.20);
        display: inline-block;
        border-radius: 10px;
        margin: 10px 0px;
        position: relative;
        cursor: pointer;
      }

      .list-mobile .progress-status{
        display: inline-block;
        width: 85%;
        margin-right: 5px;
      }

      .list-mobile .text{
        color: black;
        font-weight: bold;
        font-size: 18px;
        margin: 0px 20px;
        margin-top: 10px;
      }
    }

</style>